
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
name: 'User',
components: {
Cell,
Group
},
data() {
return {
serviceLink: '',
isLogin: false,
loginInfo: {
userName: '', // 用户名
amount: 0.0, // 可用金额
amountCanOut: 0.0, // 可提金额
surplus: 0.0 // 当日盈亏
}
};
},
computed: {},
async created() {
this.$vux.loading.show({
text: 'Loading'
});
await this.getUserInfo();
await this.service();
this.$vux.loading.hide();
},
methods: {
async getUserInfo() {
if (window.localStorage.getItem('token')) {
this.isLogin = true;
let res = await this.$http('/queryUserCommonInfo', {
body: {
userId: window.localStorage.getItem('userId'),
token: window.localStorage.getItem('token')
}
});
this.loginInfo = Object.assign(this.loginInfo, res.returnMap);
}
},

async service() {
let res = await this.$http('/queryCustomerServiceInfo');
this.serviceLink = res.returnMap.customerServiceUrl;
}
}
};



